/* Button Component */
.btn {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border-radius: 4px;
  border: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: inherit;
  display: block;
  font-size: .6rem;
  height: 1.8rem;
  line-height: 1.8rem;
  outline: 0;
  overflow: hidden;
  position: relative;
  text-align: center;
}
.btn::after {
    background-color: #000;
    content: " ";
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}
.btn:not(.is-disabled):active::after {
    opacity: .4;
}
.btn.btn-default {
    color: black;
    background-color: #f6f8fa;
    -webkit-box-shadow: 0 0 1px #b8bbbf;
            box-shadow: 0 0 1px #b8bbbf;
}
.btn.btn-default.is-plain {
      border: 1px solid #e2e2e2;
      background-color: transparent;
      -webkit-box-shadow: none;
              box-shadow: none;
}
.btn.btn-primary {
    color: #fff;
    background-color: #428bef;
    -webkit-box-shadow: 0px 1px 10px rgba(66, 139, 239, 0.5);
            box-shadow: 0px 1px 10px rgba(66, 139, 239, 0.5);
}
.btn.btn-primary.is-plain {
      border: 1px solid #428bef;
      background-color: transparent;
      color: #428bef;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}



